export default {
	name: 'SideDrawer',
	props: {
		visible: {
			type: Boolean,
			default: false
		},
		mask: {
			type: Boolean,
			default: true
		},
		maskClosable: {
			type: Boolean,
			default: true
		},
		mode: {
			type: String,
			default: 'left' // left right
		},
		height: {
			type: Number,
			default: 64
		},
		sideDraweH: {
			type: Number,
			default: 0
		}
	},
	data() {
		return {
			ps: '',
			pe: ''
		};
	},
	methods: {
		handleMaskClick() {
			if (!this.maskClosable) {
				return;
			}
			this.$emit('close');
		},
		sure() {
			let param = {};
			param.ps = this.ps;
			param.pe = this.pe;
			this.$emit('sure', param);
		},
		tapItem() {
			console.log('点击 item');
		},
		reset() {
			this.ps = '';
			this.pe = '';
		}
	}
};
